import React , { useState } from 'react';

import {
        Carousel,
        CarouselItem,
        CarouselControl,
        CarouselIndicators,
        CarouselCaption

} from 'reactstrap';

import lee from '../../media/hhf1.jpg';
import sean from '../../media/hhf2.jpg';
import one from '../../media/one.jpg';
import two from '../../media/two.jpg';
import three from '../../media/three.jpg';
import four from '../../media/four.jpg';
import five from '../../media/five.jpg';
import six from '../../media/six.jpg';
import seven from '../../media/seven.jpg';

const items = [
    {
      src: lee,
    },
    {
      src: sean,
    },
    {
      src: one,
    },
    {
      src: two,
    },
    {
      src: three,
    },
    {
      src: four,
    },
    {
      src: five,
    },
    {
      src: six,
    },
    {
      src: seven,
    }
  ];

const Notice = (props) => { 
    const [activeIndex, setActiveIndex] = useState(0);
    const [animating, setAnimating] = useState(false);

    const next = () => {
        if (animating) return;
        const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
        setActiveIndex(nextIndex);
      }

      const previous = () => {
        if (animating) return;
        const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
        setActiveIndex(nextIndex);
      }

      const goToIndex = (newIndex) => {
        if (animating) return;
        setActiveIndex(newIndex);
      }

      const slides = items.map((item) => {
        return (
          
          <CarouselItem
            onExiting={() => setAnimating(true)}
            onExited={() => setAnimating(false)}
            key={item.src}
          >
            <img src={item.src} height="250px" alt={item.altText} />
            <CarouselCaption captionText={item.caption} captionHeader={item.caption} />
          </CarouselItem>
        );
      });

      return (
        <Carousel
          activeIndex={activeIndex}
          next={next}
          previous={previous}
        >
          <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={goToIndex} />
          {slides}
          <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
          <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
        </Carousel>
      );
   
    
}


export default Notice;